.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  position: fixed;
  height: 4rem;
  z-index: 500;
  background: -webkit-gradient(linear, left top, right top, from(#f0f4c3), to(#dce775));
  background: -webkit-linear-gradient(left, #f0f4c3, #dce775);
  background: -o-linear-gradient(left, #f0f4c3, #dce775);
  background: linear-gradient(to right, #f0f4c3, #dce775);
  color: #651fff;
  font:inherit;
  font-weight: bold;
  top: 0;
  left: 0;
}

.toggleSlideShow{

}

@media (min-width: 500px) {
  .container{
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .toggleSlideShow{
    display: none;
  }
}



