.container{
  overflow: hidden;
  height: 4rem;
  width: 4rem;
}

.logoStyle{
  background: url("https://i.imgur.com/zXfd689.png") center;
  background-size: cover;
  -webkit-transform: scale(1.3) scaleX(1.3) translateY(-5px);
      -ms-transform: scale(1.3) scaleX(1.3) translateY(-5px);
          transform: scale(1.3) scaleX(1.3) translateY(-5px);
  width: 100%;
  height: 100%;
}


