.slider{
  position: fixed;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background:-webkit-gradient(linear,left top, left bottom,from(#a1ffce),to(#faffd1));
  background:-webkit-linear-gradient(top,#a1ffce,#faffd1);
  background:-o-linear-gradient(top,#a1ffce,#faffd1);
  background:linear-gradient(to bottom,#a1ffce,#faffd1);
  //transition: transform 0.5s ease-in-out;
  -webkit-transform: translateX(-200%);
      -ms-transform: translateX(-200%);
          transform: translateX(-200%);
  z-index: 10001;
}


.open{
  //transform: translateX(0);
  -webkit-animation: tilt-in-left-1 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: tilt-in-left-1 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.close{
  -webkit-animation: slide-out-blurred-left 0.8s cubic-bezier(0.755, 0.050, 0.855, 0.060) both;
          animation: slide-out-blurred-left 0.8s cubic-bezier(0.755, 0.050, 0.855, 0.060) both;
}


