.outerDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-shadow: 3px 3px 3px 3px #242425;
          box-shadow: 3px 3px 3px 3px #242425;
  width: 100%;
  background-image: -webkit-linear-gradient(337.9deg, rgb(32, 38, 57) 11.4%, rgba(63, 76, 119, 1) 70.2%);
  background-image: -o-linear-gradient(337.9deg, rgb(32, 38, 57) 11.4%, rgba(63, 76, 119, 1) 70.2%);
  background-image: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgba(63, 76, 119, 1) 70.2%);
}

.btnOrder {
  font: inherit;
  font-size: 1.5rem;
  margin: 0.5rem 0;
  background: #31b0d5;
  color: white;
  font-weight: bold;
  cursor: pointer;
  -ms-flex-item-align: center;
      align-self: center;
  outline: none;
}

.btnOrder:hover,
.btnOrder:active {
  background: #f4d004;
  color: rgba(0, 0, 0, 0.9);
}

.btnOrder:disabled {
  background: lightgray;
  color: black;
  cursor: not-allowed;
}

.btnOrder:not(:disabled) {
  //animation: btnActive 1.5s linear;
  -webkit-animation: vibrate-1 0.3s linear 4 both;
          animation: vibrate-1 0.3s linear 4 both;
}



@-webkit-keyframes vibrate-1 {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
  20% {
    -webkit-transform: translate(-2px, 2px);
    transform: translate(-2px, 2px);
  }
  40% {
    -webkit-transform: translate(-2px, -2px);
    transform: translate(-2px, -2px);
  }
  60% {
    -webkit-transform: translate(2px, 2px);
    transform: translate(2px, 2px);
  }
  80% {
    -webkit-transform: translate(2px, -2px);
    transform: translate(2px, -2px);
  }
  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}



@keyframes vibrate-1 {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
  20% {
    -webkit-transform: translate(-2px, 2px);
    transform: translate(-2px, 2px);
  }
  40% {
    -webkit-transform: translate(-2px, -2px);
    transform: translate(-2px, -2px);
  }
  60% {
    -webkit-transform: translate(2px, 2px);
    transform: translate(2px, 2px);
  }
  80% {
    -webkit-transform: translate(2px, -2px);
    transform: translate(2px, -2px);
  }
  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}


@-webkit-keyframes btnActive {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  55% {
    -webkit-transform: rotateZ(15deg);
            transform: rotateZ(15deg);
  }
  70% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  85% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
  }
  100% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}


@keyframes btnActive {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  55% {
    -webkit-transform: rotateZ(15deg);
            transform: rotateZ(15deg);
  }
  70% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  85% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
  }
  100% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}


