.backdrop {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 600ms ease-in-out,visibility 600ms;
  -o-transition: opacity 600ms ease-in-out,visibility 600ms;
  transition: opacity 600ms ease-in-out,visibility 600ms;
}

.open {
  visibility: visible;
  opacity: 1;
}


@-webkit-keyframes show {
  0% {
    width: 100vw;
    height: 100vh;
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    width: 100vw;
    height: 100vh;
    opacity: 1;
  }
}


@keyframes show {
  0% {
    width: 100vw;
    height: 100vh;
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    width: 100vw;
    height: 100vh;
    opacity: 1;
  }
}


