.modalDiv {
  width: 70%;
  min-width: 20rem;
  min-height: 20rem;
  position: fixed;
  top: 20%;
  left: 15%;
  background: #fffaf0;
  color: black;
  z-index: 2000;
  font: inherit;
  font-size: 1.5rem;
  font-style: italic;
  border: #91ce50 2px solid;
  padding: 1rem;
  -webkit-transition: visibility 600ms, opacity 600ms ease-in-out, -webkit-transform 600ms ease-in-out;
  transition: visibility 600ms, opacity 600ms ease-in-out, -webkit-transform 600ms ease-in-out;
  -o-transition: transform 600ms ease-in-out, visibility 600ms, opacity 600ms ease-in-out;
  transition: transform 600ms ease-in-out, visibility 600ms, opacity 600ms ease-in-out;
  transition: transform 600ms ease-in-out, visibility 600ms, opacity 600ms ease-in-out, -webkit-transform 600ms ease-in-out;
  -webkit-transform: translateY(-4rem);
      -ms-transform: translateY(-4rem);
          transform: translateY(-4rem);
  visibility: hidden;
  opacity: 0;
}


.open {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  visibility: visible;
  opacity: 1;
}


@media (min-width: 550px) {
  .modalDiv {
    width: 50%;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    left: 30%;
  }
}


